import * as dotenv from 'dotenv';
import * as mongoose from './database';
import * as graphql from './graphql';
import { Nuxt, Builder } from 'nuxt';
dotenv.config();
var db = mongoose.startDB();
var server = graphql.startServer(db);
var nuxt = new Nuxt(require('../nuxt.config.js'));
if (process.env.DEV_TYPE && process.env.DEV_TYPE === 'nuxt') {
    var builder = new Builder(nuxt);
    builder.build();
}
server.express.use(nuxt.render);



