import * as graphql from "graphql-yoga";
import { importSchema } from 'graphql-import';
import models from '../database/models';
import resolvers from './resolvers';
export var startServer = function (db) {
    var context = function (req) { return ({
        db: db,
        models: models,
        req: req.request
    }); };
    var port = process.env["PORT_" + process.env.DEV_TYPE] || '3000';
    var options = {
        cors: {
            credentials: true,
            methods: "*",
            origin: /^http(s?):\/\/10\.\w*/,
        },
        port: port,
        endpoint: '/graphql',
        playground: '/playground'
    };
    var server = new graphql.GraphQLServer({
        context: context,
        resolvers: resolvers,
        typeDefs: importSchema('server/graphql/schemas/index.graphql')
    });
    server.start(options, function () {
        console.log("\uD83D\uDE80\uD83D\uDE80\uD83D\uDE80 Server is running on  http://localhost:" + port);
    });
    return server;
};



