import * as mongoose from 'mongoose';
var moment = require('moment');
var UserSchema = new mongoose.Schema({
    createTime: {
        type: String,
        default: String(moment(Date.now()).format('YYYY-MM-DD HH:mm:ss'))
    },
    password: {
        type: String,
        required: true,
        trim: true,
        set: function (pwd) {
        }
    }
});
var User = mongoose.model('User', UserSchema);
export default User;



